<template>
  <el-container class="bookingRegister">
    <el-header>{{tid}}</el-header>
    <el-main>
      <el-tabs value="xq0" :stretch="true" @tab-click="handleClick" style="background: #fff;">
        <el-tab-pane  v-for="timeQs in timeQ" :key="timeQs.index" :name=timeQs.bkContent  class="p-tab" >
          <div slot="label">
            <p>{{timeQs.bkWeek}}</p>
            <p>{{timeQs.bkDate}}</p>
          </div>
          <div>{{timeQs.bkContent}}</div>
        </el-tab-pane>
      </el-tabs>
    </el-main>
  </el-container>
</template>
<style scoped>
@import "../../assets/css/outpatient.css";
</style>
<script>
import axios from "axios";
export default {
  data() {
    return {
      tid: this.$route.params.userId,
      timeQ : []
    };
  },
  mounted() {
    this.time();
  },
  methods: {
    handleClick(tab, event) {
    },
    time() {
      var time = new Date();
      var timesStamp = time.getTime();
      var day = time.getDay(); //当前系统时间中的周值
      var weeks = ["周日", "周一", "周二", "周三", "周四", "周五", "周六"];

      var timeQ = [];
      for (let i = 0; i < 5; i++) {
        day++;
        var week = weeks[day - 1];
        if (day == 7) {
          day = 0;
        }
        timeQ.push({//push成对象格式
          bkDate:
            (new Date(timesStamp + 24 * 60 * 60 * 1000 * i).getMonth() + 1)
              .toString()
              .padStart(2, "0") +
            "-" +
            new Date(timesStamp + 24 * 60 * 60 * 1000 * i)
              .getDate()
              .toString()
              .padStart(2, "0"),
          bkWeek: week,
          bkContent:"xq" + i 
        });
      }
      console.log(timeQ);
      this.timeQ = timeQ
    }
  }
};
</script>
<style>
</style>


